@import url(https://fonts.googleapis.com/css?family=Major+Mono+Display&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Nova+Mono&display=swap);
:root {
  --blue: #9cdcfe;
  --pink: #c586c0;
  --yellow:#dcdcaa;
  --green: #4ec9b0;
  --orange: #ce9178;
  --hover-black: rgb(40, 40, 40);
  --white: #fff;
  --gray: rgb(80, 80, 80);
  --light-gray: rgb(150, 150, 150);
}

*::-webkit-scrollbar {
  width: 4px;
}

/* Track */
*::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
*::-webkit-scrollbar-thumb {
  background: #9cdcfe;
  background: var(--blue);
  border-radius: 10px;
}

/* Handle on hover */
*::-webkit-scrollbar-thumb:hover {
  background: #4ec9b0;
  background: var(--green);
}

.icon {
  width: 24px;
  height: 24px;
}
@media (max-width: 769px) {
    #root .container.responsive{
        display: block;
        padding: 24px 8px 8px 8px;
    }
    .side-bar img.avatar.responsive {
        width: 104px;
    }
}
@-webkit-keyframes spin{
  from {
    -webkit-transform: rotate(00deg);
            transform: rotate(00deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin{
  from {
    -webkit-transform: rotate(00deg);
            transform: rotate(00deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

hmtl, body, #root {
  min-height: 100%;
}

span.loader {
  display: block;
  margin: 0 auto;
  height: 48px;
  width: 48px;
  border: calc(48px/8) solid transparent;
  border-top: calc(48px/8) solid var(--pink);
  border-radius: 50%;
  -webkit-animation: spin 2000ms infinite;
          animation: spin 2000ms infinite;
}
span.loader.green{
  border-top: calc(48px/8) solid var(--green);
}

body {
  background: #000;
  background-size: cover;
  -webkit-font-smoothing: antialiased !important;
}

body, input, button {
  font-family: 'Nova Mono', monospace;
  font-size: 14px;
}

#root .container {
  height: 100vh;
  width: 100%;
  display: flex;
  padding: 48px 24px 24px 24px;
}

.container .content {
  flex: 1 1;
  overflow: auto;
}

aside .side-bar {
  display: flex;
  flex-direction: column;
  position: fixed;
  overflow: hidden;
}

.side-bar .header {
  padding-right: 24px;
  display: flex;
  border-bottom: 2px solid var(--hover-black);
}

.side-bar .bio {
  flex: 1 1;
  color: var(--white);
}

.side-bar .info-list {
  display: flex;
  flex-direction: column;
  padding: 4px;
}

.side-bar .info-list span.index {
  background: var(--hover-black);
  border-bottom: 1px solid #333;
  color: var(--white);
}

.side-bar .header .chart {
  flex: 1 1;
  display: inline-block;
}

.side-bar img.avatar {
  align-self: flex-end;
  margin-left: 12px;
  width: 156px;
  border-radius: 8px;
  margin-bottom: 16px;
}

/* font-family: 'Major Mono Display', monospace; */
.project-container {
    color: var(--white);
    padding: 4px 8px;
    border-left: 2px solid var(--gray);
    margin: 8px 0;
    background-position: left;
    background-image: -webkit-gradient(linear, right top, left top, from(transparent), color-stop(50%, transparent), color-stop(50%, var(--hover-black)), to(var(--hover-black)));
    background-image: linear-gradient(to left, transparent, transparent 50%, var(--hover-black) 50%, var(--hover-black));
    background-position: 100% 0;
    background-size: 200% 100%;
    -webkit-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}

.project-container:hover {
    background-position: 0 0;
    border-left: 2px solid var(--blue);
}

.project-container .header {
    display: flex;
    flex-direction: column;
}

.project-container .header .more-info {
    display: flex;
    flex-direction: row-reverse;
}

.project-container .header .stargazers-count {
    display: inline-flex;
    align-items: center;
}

.project-container .header .stargazers-count .count {
    font-size: 18px;
    margin: 0 8px;
}

.project-container .header .repo-info .created-at{
    color: var(--light-gray);
    font-size: 12px;
}

.project-container .header .repo-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.project-container .header .repo-info .repo-language {
    display: flex;
    align-items: center;
}

.project-container .header .repo-info .repo-language .icon {
    width: 24px;
}

.project-container .header .repo-info .repo-language .name {
    margin: 0 8px;
}

.project-container .header .more-info .repo-link {
    align-self: flex-end;
    width: 32px;
    height: 32px;
    background: url(/git-cv/static/media/folder-github.a91ad7c2.svg)
}

.project-container .header .more-info .repo-link:active {
    width: 32px;
    height: 32px;
    background: url(/git-cv/static/media/folder-github-open.4472aabe.svg)
}
@media(max-width: 769px) {
    .import.import-responsive {
        font-size: 14px;
        margin-bottom: 4px;
    }
    .import strong.responsive {
        font-size: 12px;
    }
    span.tooltip.responsive:hover::before {
        width: 80%;
        margin:0;
    }
    span.tooltip:hover::after {
        content: ' ';
        -webkit-transform: translate(-368px, 70%);
                transform: translate(-368px, 70%);
        position: absolute;
        display: inline-block;
        z-index: 1;
        margin: -20px 0 0 0;
        border-width: 8px;
        border-style: solid;
        border-color: var(--hover-black) transparent transparent transparent;
    }
}
.import {
    text-align: left;
    color: var(--blue);
    font-size: 18px;
    font-weight: normal;
    margin-bottom: 4px;
}

.import:hover {
    background: var(--hover-black);
}

.import a:target {
    background: var(--hover-black);
}

.import a {
    color: var(--blue);
    text-decoration: none;
}

.import a:visited {
    color: var(--blue);
}

.import strong {
    font-size: 16px;
    color: var(--pink);
}

.import strong.tab {
    margin-left: 20px;
}

span.tooltip:hover::before {
    padding: 4px;
    font-size: 12px;
    -webkit-transform: translateY(-115%);
            transform: translateY(-115%);
    width: 450px;
    content: attr(aria-label);
    position: absolute;
    display: inline-block;
    z-index: 1;
    color: var(--white);
    background: var(--hover-black);
}

span.tooltip:hover::after {
    content: ' ';
    -webkit-transform: translate(-368px, 70%);
            transform: translate(-368px, 70%);
    position: absolute;
    display: inline-block;
    z-index: 1;
    margin: -20px 0 0 0;
    border-width: 8px;
    border-style: solid;
    border-color: var(--hover-black) transparent transparent transparent;
}
@media(max-width: 769px) {
    .package.responsive{
        font-size: 12px;
    }   
    .package .label.responsive {
        font-size: 14px;
        margin-bottom: 14px;
    }
}
.package .label {
    display: block;
    background: var(--hover-black);
    font-size: 16px;
    margin-bottom: 16px;
    padding: 2px 8px;
}

.package .key {
    color: var(--blue);
}

.package .value {
    color: var(--orange);
}
